<template>
  <div id="articleIndex">
    <Card v-show="id == ''">
        文章分类查询：
        <Select v-model="articleType" style="width:200px" @on-change="changeType">
            <Option v-for="item in articleList" :value="item.type_num" :key="item.value">{{ item.type_name }}</Option>
        </Select>&nbsp;&nbsp;&nbsp;&nbsp;
        标签分类查询：
        <Select v-model="tagType" style="width:200px" @on-change="changeTag">
            <Option v-for="item in tagList" :value="item.type" :key="item.value">{{ item.name }}</Option>
        </Select>
    </Card>
    <Card v-show="id == ''">
        <p slot="title">所有文章</p> 
        <div style="text-align:center;">
            <Table :loading="loading" :columns="columns" :data="allData" stripe></Table> 
            <Page v-if="pageLength[0]" :total="pageLength[0].COUNT" :page-size="5" class="page" @on-change="changePage"></Page>                  
        </div>        
    </Card>
    <Card v-show="id">
        <articleComponent :detail="detail" v-on:changeId="recovery"/>
    </Card>
  </div>
</template>

<script>
import filters from '@/filters/index'
import articleComponent from '../articleUpdate/release'
export default {
    name: 'articleIndex',
    data(){
        return {
            loading: false,      
            id: '',            
            detail: [],
            columns: [
                {
                    title: '编号',
                    key: 'id'
                },
                {
                    title: '标题',
                    key: 'title'
                },
                {
                    title: "文章类型",
                    key: "type_name",
                    render: (h,params) => {
                        return h('div',[
                                h('Tag', {
                                    props: {
                                        color: 'blue',
                                        checkable: 'checkable' 
                                    }                                 
                                }, `${params.row.type_name}`)
                            ]
                        )
                    }
                },
                {
                    title: "标签类型",
                    key: "name",
                    render: (h,params) => {
                       if(params.row.name !== null){
                            return h('div',[
                                    h('Tag', {
                                        props: {
                                            color: 'green',
                                            checkable: 'checkable' 
                                        }                                 
                                    }, `${params.row.name}`)
                                ]
                            )
                       }else{
                           return h('div','无');
                       }
                    }
                },
                {
                    title: '作者',
                    key: 'username'
                },
                {
                    title: '发表时间',
                    key: 'post_time',
                    render: (h, params) => {                         
                        return h('div',
                            filters.timeFormat(params.row.post_time) 
                        )
                    } 
                },
                {
                    title: '阅读量',
                    key: 'read_num'
                },
                {
                    title: '操作',
                    key: 'action',
                    width: 150,
                    align: 'center',
                    render: (h, params) => {
                        return h('div', [     
                            h('Button', {
                                props: {
                                    type: 'primary',
                                    size: 'small'
                                },
                                style: {
                                    marginRight: '5px'
                                },
                                on: {
                                    click: () => {
                                        this.id = params.row.id;     
                                        if(this.id) {
                                            this.$http.get('/article/selectDetail',{
                                                params:{
                                                    "articleId": this.id
                                                }            
                                            }).then(res => {                                             
                                                this.detail = res.data.msg;                
                                            }).catch(err => {
                                                this.$Message.error("出错啦。。。")
                                            })
                                        }                                   
                                    }
                                }
                            }, '修改'),                           
                            h('Button', {
                                props: {
                                    type: 'error',
                                    size: 'small'
                                },                                
                                on: {
                                    click: () => {
                                        this.delArticle(this.allData[params.index].id,0,params.index);
                                    }
                                }
                            }, '删除')                      
                        ]);
                    }
                }
            ],
            allData: [],
            tagList: [],
            articleList: [],
            tagType: "",
            articleType: "",
            tagStatus: false,
            typeStatus: false,
            pageLength: []            
        }
    },
    watch:{
        tagType () {
            this.tagStatus = true;
            this.typeStatus = false;
        },
        articleType () {
            this.typeStatus = true;
            this.tagStatus = false;
        }
    },
    created(){       
        this.getArticle(1)      
        this.$http.get('/tag/sellectAllTag')
            .then(res => {
                this.tagList = res.data.msg;
            })    
        this.$http.get('/article/selectAllType')
            .then(res => {
                this.articleList = res.data.msg;
            }) 
    },
    methods: {
        delArticle (id,flag,index) {
            this.$http.get('/article/delArticle',{
                params: {
                    'articleId': id
                }
            }).then(res => {
                if(flag){
                    this.topData.splice(index,1);
                    this.$Message.info('删除成功！');
                }else{
                    this.allData.splice(index,1);
                    this.$Message.info('删除成功！');
                }
            }).catch(err => {
                this.$Message.error('出错啦！');
            })
        },
        changeTag (tagType) {
            this.getTagArticle(tagType,1);    
        },
        changeType(typeNum){
            this.getTypeArticle(typeNum,1);
        },
        getTagArticle(tagType,page){
            this.$http.get('/article/tagArticle',{
                params: {
                    'labelNum':tagType,
                    'page': page
                }
            }).then(res => {
                this.allData = res.data.msg;
                this.pageLength = res.data.length;
            }).catch(err => {
                this.$Message.info("加载失败。。。");
            })
        },
        getTypeArticle(typeNum,page){
            this.$http.get('/article/selectType',{
                params: {
                    "typeNum":typeNum,
                    "page": page
                }
            }).then(res => {
                this.allData = res.data.msg;
                this.pageLength = res.data.length;
            }).catch(err => {
                this.$Message.error("出错啦。。。")
            })
        },
        getArticle(page){
            this.$http.get('/article/allArticle',{
                params: {
                    "page":page
                }
            }).then(res => {            
                this.allData = res.data.msg;
                this.pageLength = res.data.length;                
            }).catch(err => {
                this.$Message.error('出错啦！');
            }) 
        },
        changePage(page){
            if(this.tagStatus){
                this.getTagArticle(this.tagType,page)
            }else if(this.typeStatus){
                this.getTypeArticle(this.articleType,page)
            }else{
                this.getArticle(page);
            }
        },
        recovery() {            
            this.id = ''
            this.getArticle(1) 
        }
    },
    components: {
        articleComponent
    }
}
</script>
<style lang="scss" scoped>
.page{
    margin-top: 10px;
}
</style>




